export function updateUseProxyForYoutubeVideoPref(
  { commit },
  useProxyForYoutubeVideoPref
) {
  commit("setUseProxyForYoutubeVideoPref", useProxyForYoutubeVideoPref);
}

export function addToHistory({ commit }, newView) {
  commit("addToHistory", newView);
}

export function removeFromHistory({ commit }, item) {
  commit("removeFromHistory", item);
}
