export function setUseProxyForYoutubeVideoPref(state, val) {
  state.useProxyForYoutubeVideoPref = val;
}

export function addToHistory(state, val) {
  state.history.splice(0, 0, val);
  for (let i = 1; i < state.history.length; i++) {
    if (val.id === state.history[i].id) {
      state.history.splice(i, 1);
      break;
    }
  }
  state.history.splice(20); // keep latest 20
}

export function removeFromHistory(state, val) {
  for (let i = 0; i < state.history.length; i++) {
    if (val.id === state.history[i].id) {
      state.history.splice(i, 1);
      console.log(`deleted ${val.id}`);
      break;
    }
  }
}
