import Vue from "vue";
import App2 from "./App2.vue";
import Parse from "parse";

Parse.initialize(
  process.env.VUE_APP_PARSE_APP_ID, // YOUR APP ID
  process.env.VUE_APP_PARSE_JS_KEY // YOUR Javascript  KEY
);
// YOUR SERVER URL
Parse.serverURL = process.env.VUE_APP_PARSE_SERVER_URL;

import video from "./store/video";
import Router from "vue-router";
Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: "/:videoId?",
      name: "home",
      component: () => import("@/views/Video")
    }
  ]
});

Vue.config.productionTip = false;

// Install BootstrapVue
import { BootstrapVue, BootstrapVueIcons } from "bootstrap-vue";
Vue.use(BootstrapVue);
Vue.use(BootstrapVueIcons);

new Vue({ router, store: video, render: h => h(App2) }).$mount("#app2");
